import styled from "styled-components"
import theme from "./../styles/theme"
import { device } from "../media-queries"

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 100vw;
  background-color: ${theme.colors.creamyWhite};
  overflow: hidden;
  padding: 0 20px;

  @media ${device.mobileS} {
    height: 500px;
  }
  @media ${device.tablet} {
    height: 700px;
  }
`

const PageHeading = styled.div`
  font-size: ${theme.fontSizes.head32};
  font-family: DroidSans-Bold;
  color: ${theme.colors.darkBlue};

  @media ${device.mobileS} {
    margin: 20px auto 80px;
  }
  @media ${device.tablet} {
    margin: 135px 0 80px;
  }
`

const EmailInput = styled.input`
  border: 1px solid #cccccc;
  border-radius: 5px;
  color: ${theme.colors.black};
  font-size: ${theme.fontSizes.n14};
  font-family: DroidSans;
  padding: 5px 10px;
  outline: none;

  width: 100%;
  max-width: 500px;
`

const MessageInput = styled.textarea`
  border: 1px solid #cccccc;
  border-radius: 5px;
  color: ${theme.colors.black};
  font-size: ${theme.fontSizes.n14};
  font-family: DroidSans;

  width: 100%;
  max-width: 500px;

  min-height: 125px;
  max-height: 125px;

  padding: 5px 10px;
  margin-top: 20px;
  outline: none;
`

const Button = styled.div`
  margin-top: 20px;
  cursor: pointer;
  border-radius: 15px;
  background-color: ${({ disabled }) =>
    disabled ? "#c0c0c0" : theme.colors.darkBlue};
  color: ${theme.colors.white};
  font-family: DroidSans;
  font-size: ${theme.fontSizes.n14};
  padding: 15px;

  &:hover {
    background-color: ${({ disabled }) =>
      disabled ? "#c0c0c0" : theme.colors.bioBg};
  }
`

const NotificationContainer = styled.div`
  position: fixed;
  z-index: 11;

  @media ${device.mobileS} {
    top: 20px;
    left: 20px;
    right: 20px;
  }

  @media ${device.tablet} {
    top: 100px;
    right: 40px;
    left: initial;
    width: 500px;
  }
`

const ErrorMessage = styled.div`
  margin: 5px 0;
  font-family: Roboto-Light;
  font-size: ${theme.fontSizes.s12};
  color: #ed4337;
`

export {
  Container,
  PageHeading,
  EmailInput,
  MessageInput,
  Button,
  NotificationContainer,
  ErrorMessage,
}
