import { useCallback } from "react"
import Http from "../http"

function useEmailNotification() {
  return useCallback((email, message) => {
    const url = process.env.AWS_NOTIFICATION_URL

    Http.post(url, { email, message })
      .then(() => console.log("Notification: ok"))
      .catch(() => console.error("Notification: failed"))
  }, [])
}

export default useEmailNotification
