import { Notification, ThemeProvider } from "gatsby-interface"
import React, { useEffect, useState } from "react"
import EmailRegex from "../email.regex"
import useEmailNotification from "../hooks/email.hook"
import useFormField from "../hooks/field.hook"
import {
  Button,
  Container,
  EmailInput,
  MessageInput,
  NotificationContainer,
  PageHeading,
  ErrorMessage,
} from "./contact.styles"

function Contact() {
  const [isErrorVisible, setErrorVisible] = useState(false)
  const [isButtonDisabled, setButtonDisabled] = useState(true)
  const email = useFormField("")
  const message = useFormField("")
  const sendEmail = useEmailNotification()

  const { isOpenedSnackbar, showNotification } = useSnackbar()
  const handleSendClick = () => {
    if (EmailRegex.test(email.value)) {
      showNotification()
      sendEmail(email.value, message.value)
    }
  }
  const handleEmailBlur = () => {
    const isValidEmail = EmailRegex.test(email.value)
    setErrorVisible(email.value === "" || !isValidEmail)
    setButtonDisabled(!isValidEmail)
  }

  return (
    <Container id="contact">
      <PageHeading>Reach me out</PageHeading>
      <EmailInput
        id="email"
        placeholder="Your email"
        type="email"
        name="email"
        onBlur={handleEmailBlur}
        {...email}
      />
      {isErrorVisible && (
        <ErrorMessage>
          Please, input a valid email address (e.g. your.email@domain.com)
        </ErrorMessage>
      )}
      <MessageInput
        id="message"
        placeholder="Your message"
        name="message"
        {...message}
      />
      <Button disabled={isButtonDisabled} onClick={handleSendClick}>
        SEND MESSAGE
      </Button>

      <NotificationContainer>
        <ThemeProvider>
          <Notification
            variant="PRIMARY"
            size="M"
            tone="SUCCESS"
            isOpened={isOpenedSnackbar}
            content="Thank you! Your message has been sent. Please, wait for my reply :)"
          />
        </ThemeProvider>
      </NotificationContainer>
    </Container>
  )
}

function useSnackbar() {
  const [isOpenedSnackbar, setIsOpenedSnackbar] = useState(false)

  useEffect(() => {
    if (isOpenedSnackbar) {
      setTimeout(() => setIsOpenedSnackbar(false), 5000)
    }
  }, [isOpenedSnackbar])

  return { isOpenedSnackbar, showNotification: () => setIsOpenedSnackbar(true) }
}

export default Contact
